import React, {useState} from 'react';
import ArrayNumberPicker from "../../../components/ArrayNumberPicker";

const DanhDe = () => {
	const [arrayPicker, setArrrayPicker] = React.useState([])
	React.useEffect(()=>{
		let update = [];
		for(let i = 0; i < 100; i++) {
			update.push(i)
		}
		setArrrayPicker(update);
	},[]);
	const [tabst, setTabst] = useState(1);
	const  toggleTab=(index)=>{
		setTabst(index);
	}
	return (
		<div className={"danh-de"}>
			<div id="tabBetType" className="bb-1">
				<div className={tabst === 1?"kd1 act":"kd1"} id="loto_type_info" onClick={()=>toggleTab(1)}> u
				</div>
				<div className={tabst === 2?"kd1 act":"kd1"}  onClick={()=>toggleTab(2)} id="loto_type_info"> c bit
				</div>
				<div className="clearfix"/>
			</div>
			<div className="table">
				<div className="col-xs-9">
					<div className="info-box">
						<table style={{width: '100%', height: '100%'}}>
							<tbody>
							<tr>
								<td colSpan="10" style={{border:'none'}}>
									<table style={{width: '100%', height: '100%'}}>
										<tbody className="table_bet_pick">
										<ArrayNumberPicker arrays={arrayPicker} indexPlus={"0"} t={false}/>
										</tbody>
									</table>
								</td>
							</tr>
							</tbody>
						</table>
					</div>
				</div>
			</div>
			<div className={"role"}>
				{tabst === 1 ? <p>
					nh l gii 7 ( c 4 gii, thanh ton  ). Thng gp 95 ln. V d : nh 1k cho s 79, Tng thanh ton: 1k x 4 =4k. Nu trong l gii 7 c 1 s 79 th Tin thng: 1k x 95 = 95k.
				</p>:<p>
					nh 2 ch s cui trong gii B. Thng gp 95 ln. V d: nh 1k cho s 79. Tng thanh ton: 1k. Nu gii B l xxx79 th Tin thng: 1k x 95 = 95k.
				</p>}
			</div>
		</div>
	);

};

export default DanhDe;