import React, {useState} from 'react';
import ArrayNumberPicker from "../../../components/ArrayNumberPicker";

const LoXien = () => {
	const [arrayPicker, setArrrayPicker] = React.useState([])
	React.useEffect(()=>{
		let update = [];
		for(let i = 0; i < 100; i++) {
			update.push(i)
		}
		setArrrayPicker(update);
	},[]);
	const [tabst, setTabst] = useState(1);
	const  toggleTab=(index)=>{
		setTabst(index);
	}
	return (
		<div className={"lo-xien"}>
			<div id="tabBetType" className="bb-1">
				<div className={tabst === 1?"kd1 act":"kd1"} onClick={()=>toggleTab(1)}>Xin 2
				</div>
				<div className={tabst === 2?"kd1 act":"kd1"}  onClick={()=>toggleTab(2)} >Xin 3
				</div>
				<div className={tabst === 3?"kd1 act":"kd1"}  onClick={()=>toggleTab(3)} >Xin 4
				</div>
				<div className="clearfix"/>
			</div>
			<div className="table">
				<div className="col-xs-9">
					<div className="info-box">
						<table style={{width: '100%', height: '100%'}}>
							<tbody>
							<tr>
								<td colSpan="10" style={{border:'none'}}>
									<table style={{width: '100%', height: '100%'}}>
										<tbody className="table_bet_pick">
										<ArrayNumberPicker arrays={arrayPicker} indexPlus={"0"} t={false}/>
										</tbody>
									</table>
								</td>
							</tr>
							</tbody>
						</table>
					</div>
				</div>
			</div>
			<div className={"role"}>
				{tabst === 1 ? <p>
					Xin 2 ca 2 ch s cui trong l 27 gii. Thng gp 17 ln. V d : nh 1k cho xin 11+13, Tng thanh ton: 1k. Nu trong l c "2 ch s cui l 11 v 2 ch s cui l 13" th Tin thng: 1k x 17 = 17k
				</p>:tabst===2?<p>
					Xin 3 ca 2 ch s cui trong l 27 gii. Thng gp 65 ln. V d : nh 1k cho xin 11+13+15, Tng thanh ton: 1k. Nu trong l c 2 ch s cui l 11,13,15 th Tin thng: 1k x 65 = 65k
				</p>:<p>
					Xin 4 ca 2 ch s cui trong l 27 gii. Thng gp 250 ln. V d : nh 1k cho xin 11+13+15+20, Tng thanh ton: 1k. Nu trong l c 2 ch s cui l 11,13,15,20 th Tin thng: 1k x 250 = 250k
				</p>}
			</div>
		</div>
	);
};

export default LoXien;